body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  border: 0;
}

body {
  color: #8b8b8b;
  font-family: "Open Sans";
  font-size: 16px;
}

.header {
  background-image: url(../img/fbg.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  height: 110vh;
}

.container {
  max-width: 1200px;
  padding-left: 0;
  padding-right: 0;
  margin: 0 auto;
}

.row {
  padding-top: 50px;
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: space-around;
}

.logo {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(16.666666666666668% - 30px);
  width: 147px;
}

.menu {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(8.333333333333334% - 30px);
  color: #ffffff;
  font-size: 24px;
}

.header__title {
  margin-top: 190px;
  color: #ffffff;
  font-family: "Open Sans";
  font-size: 52px;
  font-weight: 300;
  letter-spacing: 2.08px;
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  justify-content: center;
}

form {
  margin-top: 27px;
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  justify-content: center;
}

form input {
  width: 326px;
  height: 40px;
  border: 0;
  padding-left: 10px;
  font-size: 16px;
}

form .btn {
  padding: 10px 49px;
  background-color: #fd4f32;
  color: #ffffff;
  font-weight: 700;
  letter-spacing: 0.64px;
}

.destinations__wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.card {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(25% - 30px);
  box-shadow: 0 2px 0 #e1e0d8;
  margin-bottom: 20px;
  position: relative;
  text-align: center;
}

.card span {
  color: #ffffff;
  font-family: Montserrat;
  font-size: 12px;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 0.24px;
  position: absolute;
  right: 6%;
  bottom: 36%;
}

.card img {
  width: 280px;
}

.card .card__title {
  color: #323232;
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 0.28px;
  padding: 20px;
}

.card .card__text {
  color: #696969;
  font-size: 13px;
  font-weight: 400;
  line-height: 19px;
  padding: 20px;
  padding-top: 0;
  padding-bottom: 17px;
}

.section__title {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(100% - 30px);
  margin-top: 60px;
  margin-left: 0;
  color: #323232;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.32px;
}

.section__subtitle {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(100% - 30px);
  margin-left: 0;
  margin-top: 13px;
  margin-bottom: 36px;
  font-size: 15px;
  font-weight: 400;
  letter-spacing: 0.3px;
}

.advantages__title {
  margin-top: 150px;
  margin-bottom: 56px;
  color: #323232;
  font-family: "Open Sans";
  font-size: 52px;
  font-weight: 300;
  letter-spacing: 2.08px;
}

.advantages__wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.item {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(33.333333333333336% - 30px);
}

.item__title {
  color: #323232;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 0.32px;
  margin-bottom: 18px;
}

.item__text {
  color: #8b8b8b;
  font-family: "Open Sans";
  font-size: 15px;
  font-weight: 400;
  line-height: 22px;
  letter-spacing: 0.3px;
  margin-bottom: 108px;
}

footer {
  padding: 20px;
}

.footer__wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  align-items: center;
}

.footer__wrapper .footer__text {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(50% - 30px);
}

.footer__wrapper .footer__nav {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(33.333333333333336% - 30px);
}

.footer__wrapper .footer__nav .nav__list {
  display: flex;
  justify-content: center;
}

.footer__wrapper .footer__nav .nav__list li {
  padding: 10px;
}

.footer__wrapper .footer__nav .nav__list a {
  text-decoration: none;
  color: #323232;
  text-transform: uppercase;
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 600;
}

@media screen and (max-width: 1100px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (max-width: 960px) {
  .container {
    padding-left: undefined;
    padding-right: undefined;
  }
}

@media screen and (max-width: 780px) {
  .container {
    padding-left: undefined;
    padding-right: undefined;
  }

  .header__title {
    font-size: 40px;
  }

  .card {
    width: calc(50% - 30px);
  }

  .item {
    width: calc(50% - 30px);
  }
}

@media screen and (max-width: 560px) {
  .container {
    padding-left: undefined;
    padding-right: undefined;
  }

  .header__title {
    font-size: 25px;
  }

  form {
    flex-direction: column;
  }

  form input {
    width: auto;
  }

  form .btn {
    width: auto;
  }

  .card {
    width: calc(100% - 30px);
  }

  .item {
    width: calc(100% - 30px);
  }

  .footer__wrapper .footer__text {
    width: calc(100% - 30px);
  }

  .footer__wrapper .footer__text {
    text-align: center;
  }

  .footer__wrapper .footer__nav {
    width: calc(100% - 30px);
  }

  .footer__wrapper .footer__nav .nav__list {
    flex-direction: column;
  }

  .footer__wrapper .footer__nav .nav__list {
    align-items: center;
  }

  .footer__wrapper .footer__nav .nav__list {
    margin-top: 10px;
  }
}